header {
    flex: 0 0 36px;
  
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #303030;
  }
  
  header h1 {
    display: block;
    font-family: Motorola;
    letter-spacing: 2pt;
    font-size: 4rem;
    color: white;
  }
  
  header h2 {
    color: #cfcfcf;
  }
  
  header h1 {
    margin: 0 10px;
  }

  header .header-buttons {
    height: 100%;
    width: 300px;
    display: flex;
    justify-content: space-between;
    background: #eef2f3;
  }

  header .header-buttons .create-sprite:hover,
  header .header-buttons .logout-button:hover,
  header .header-buttons .login-button:hover {
    background: #fff1a2;
  }
  
  header .create-sprite {
    width: 100px; 
    font-weight: bold;
    border: none;
    padding: 3px 5px;
  }
  
  header .login-button {
    width: 200px; 
    font-weight: bold;
    border: none;
    padding: 3px 5px;
    border-left: 1px solid gray;
  }

  header .logged-on {
    height: 100%;
    width: 200px;
    display: flex;
    justify-content: space-between;
  }

  header .user-info {
      display: flex;
      width: 140px;
      justify-content: space-around;
      align-items: center;
      border-left: 1px solid gray;
      font-size: 1.4rem;
      font-weight: bold;
  }

  header .user-info:hover {
      background: none;
  }

  header .logout-button {
    border-left: 1px solid gray;
    width: 60px;
    font-weight: bold;
  }

  header .user-picture {
      border-radius: 100%;
  }
