.layer-pad {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 5px 0;
}

.layer-pad .player {
  width: 200px;
  height: 230px;
}

.player-canvas-container {
  width: 200px;
  height: 200px;
}

.player-canvas {
  width: 100%;
  height: 100%;
  background: url(../screens/background-image.png);
}

.player-controls {
  display: flex;
  justify-content: space-between;
  background: #303030;
}

button.fullscreen {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 40px;
  font-size: 3rem;
  color: white;
  border-right: 1px solid gray;
}

.player-fps {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 40px;
  color: white;
  font-size: 1.2rem;
}

#input-fps {
  margin-right: 5px;
  width: 120px;
  -webkit-appearance: none;
  background: none;

}

#input-fps::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 24px;
  width:  4px;
  background: white;
}

#input-fps:focus {
  outline: none;
}

#input-fps::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  background: white;
  display: flex;
  align-items: center;
}

.layers,
.select-container,
.transform-container {
  box-sizing: border-box;
  width: 200px;
  display: flex;
  flex-direction: column;
  margin: 15px 0;
  border: 3px solid gray;
  background: gray;
}

.layers > * {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.component-name {
  height: 35px;
  border-bottom: 1px solid gray;
  background: #303030;
}

.component-name div {
  width: 80px;
  height: 35px;
  /* text-align: center; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-weight: bold;
  color: white;
}

.show-all-layers {
  width: 35px;
  height: 35px;
  font-size: 2.5rem;
  color: white;
}

.show-all-layers.checked { 
  color: #ffd900;
}

.layers-control {
  border-bottom: 1px solid gray;
  height: 35px;
}

.layers-control > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 47px;
  height: 35px;
  color: white;
  font-size: 2rem;
  background: #303030;
}

.layers-list {
  display: flex;
  flex-direction: column;
  color: white;
  font-size: 1.5rem;
  background: #505050;
}

.layers-list > * {
  padding: 2px 10px;
  border-bottom: 1px solid gray;
}

.layers-list > *:hover {
  background: #6d6d6d;
}

.layers-list > *.selected {
  background: #6d6d6d;
}

.select-container,
.transform-container {
  background: #303030;
}

.select-container p,
.transform-container p {
  color: white;
  font-size: 2rem;
  padding: 5px 10px;
  font-weight: bold;
}

.select-container select {
  background: #b3b3b3;
  border: none;
  padding: 3px;
  font-weight: bold;
}

.select-container select:focus {
  outline: none;
}

.transform-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 2px solid gray;
  background: #505050;
}

.transform-buttons > * {
  border-right: 2px solid gray;
  width: 65px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.transform-buttons > *:hover {
  background: gray;
}

.transform-buttons .rotate {
  border-right: none;
}

.transform-buttons .vertical-mirror::after {
  content: url(../images/transform/tool-flip.png);
}

.transform-buttons .horisontal-mirror::after { 
  content: '‚•§';
  color: white;
  font-size: 4rem;
  
}

.transform-buttons .rotate::after {
  content: url(../images/transform/tool-rotate.png);
}

.canvas-info {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0 10px;
  color: #ffd900;
}

.canvas-info p  {
  width: 110px;
  display: flex;
  justify-content: space-between;
}

