@font-face {
  font-family: 'Motorola';
  src: url(fonts/pix.ttf);
}

@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  src: url(fonts/Roboto/Roboto-Regular.ttf);
}

@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  src: url(fonts/Roboto/Roboto-Bold.ttf);
}

* {
  margin: 0;
  padding: 0;
}
  
html {
  font-size: 10px;
}
  
body {
  height: 100vh;
  width: 100vw;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

canvas {
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-crisp-edges;
  image-rendering: pixelated;
  image-rendering: crisp-edges;
}

button {
  background: none;
  border: none;
}

#root {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: stretch;
  background: #3f3f3f;
}

main {
  flex: 1 1 auto;
  overflow: hidden;
  width: 100%;
  display: grid;
  grid-template-columns: 100px 117px 1fr 210px 50px;
}

.canvas-pad {
  display: grid;
  grid-template: 1fr auto 1fr / 1fr auto 1fr;
  background: gray;
  overflow: hidden;
  margin: 5px 0;
  /* display: flex;
  align-items: center;
  justify-content: center; */
}

.canvas-pad canvas {
  grid-column: 2;
  grid-row: 2;
}

.background-canvas {
  z-index: 6;
  background: url(./screens/background-image-4px.png);
}

.layers-below-canvas {
  z-index: 8;
}

.draw-canvas {
  z-index: 9;
  cursor: crosshair;
}

.layers-above-canvas {
  z-index: 10;
}

.pixel-glow-canvas {
  z-index: 11;
  /* background: gray; */
}

/* –≠–¢–û –õ–ï–ù–î–ò–ù–ì */

main.landing-box {
  display: flex;
  justify-content: space-around; 
  align-items: center;
}

.landing-screenshot {
  width: 64vw;
  height: 36vw;
  background: url(images/app-screenshot.png);
  background-size: cover;
  border: 3px solid gray;
}

.landing-info {
  display: flex;
  flex-direction: column; 
  color: white;
  width: 420px;
  height: 36vw;
}

.landing-h1 {
  font-family: 'motorola';
  font-size: 10rem;
}

.landing-p {
  font-size: 3rem;
  text-align: justify;
}

.landing-button {
  font-weight: bold;
  font-size: 1.5rem;
  align-self: center;
  background: #ffd900;
  padding: 10px;
  margin-top: 10vw;
  border-radius: 3px;
}

.landing-button:hover {
  background: #fff1a2;
}


  