.key-bind-overlay {
    display: none;
    position: absolute;
    z-index: 16;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: #0000006c;
}

.key-bind-overlay.active {
    display: flex;
    justify-content: center;
    align-items: center;
}

.key-bind-window {
    z-index: 17;
    width: 800px;
    height: 480px;
    position: relative;
    background: #444444;
    border: 3px solid #303030;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    color: white;
}

.key-bind-container {
    width: 750px;
    height: 300px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-items: center;
    align-items: center;
}

.key-bind-container > div {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 180px;
    height: 47px;
    background: #303030;
    flex-basis: 25%;
    
}

.key-bind-container p {
    background: #6d5d00;
    width: 120px;
    height: 25px;
    font-size: 2rem;
    text-align: center;
    border: 2px solid gray;
}

.key-bind-container p.active {
    background: #e0bf00;
}

.key-bind-window .close-window {
    color: #ffd900;
    position: absolute;
    top: 0;
    right: 0;
    margin: 5px;
}

.key-bind-window .pen-image::after {
    content: url(../images/tools/tool-pen.png);
}

