.export-window-overlay {
  display: none;
  z-index: 14;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #0000006c;
}

.export-window-overlay.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

.export-window {
    z-index: 15;
    width: 360px;
    height: 360px;
    background: rgb(68, 68, 68);
    border: 3px solid rgb(48, 48, 48);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    color: white;
}

.export-window label {
  height: 30px;
}

.export-window legend {
  font-size: 3rem;
  color: #ffd900;
}

.export-window label {
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.5rem;
}

.export-window [type='text'],
.export-window select,
.export-window [type='range'],
.export-window [type='color'] {
  box-sizing: border-box;
  width: 170px;
  background-color: none;
  padding: 0;
}

.export-window input[type="color"] {
  -webkit-appearance: none;
  border: none;
  background: none;
}

.export-window input[type="range"] {
  -webkit-appearance: none;
  background: none;
}

.export-window input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 24px;
  width:  4px;
  background: white;
}

.export-window input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  background: white;
  display: flex;
  align-items: center;
}

.export-window input[type="range"]:focus {
  outline: none;
}

#export-submit-button {
  width: 80px;
  height: 30px;
  background: #ffe343;
  font-weight: bold;
  font-size: 1.5rem;
}

#export-submit-button:hover {
  background: #fff19f;
}

#encoder-close {
  color: #ffe343;
  position: absolute;
  top: 0;
  right: 0;
  margin: 4px;
  font-size: 1.5rem;
}

#encoder-close:hover {
  color: #ffffff;
}
