section.menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}

section.menu > * {
    box-sizing: border-box;
    width: 46px;
    height: 46px;

    color: white;
    font-weight: bold;
    font-size: 2rem;
    border-radius: 5px 0 0 5px;
    margin: 4px 0;
    background: #272727;
}

section.menu > *:hover {
    background: #535353;
}

button.export::after {
  content: url(../images/settings-save-white.png);
}
