.frame-pad {
    overflow-y: scroll;
    margin: 5px 0;
  }

::-webkit-scrollbar { width: 3px; height: 3px;}
::-webkit-scrollbar-button {  display: none;  }
::-webkit-scrollbar-track {  background-color: #4d4d4d;;}
::-webkit-scrollbar-track-piece { background-color: #4d4d4d;}
::-webkit-scrollbar-thumb { height: 50px; background-color: #a7a7a7; }
::-webkit-scrollbar-corner { background-color: #999;}
::-webkit-resizer { background-color: #4d4d4d;;}
  
  .frame-pad > * {
    box-sizing: border-box;
    position: relative;
    width: 90px;
    height: 90px;
    margin: 5px 0 5px 13px;
    border: 3px solid gray;
  }
