  }
  
  .frame-pad canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    background: center url(../../screens/background-image.png);
  }

  .frame > button,
  .frame > div {
    z-index: 2;
    box-sizing: border-box;
    position: absolute;
    width: 26px;
    height: 26px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .frame.selected {
    border: 3px solid #ffd900;
  }

  .delete-frame {
    bottom: 0;
    right: 0;
    background: center url(../../images/frame/frame-recyclebin-white.png),
              #4d4d4d;
    border: none;
    border-left: 3px solid gray;
    border-top: 3px solid gray;
  }

  .duplicate-frame {
    bottom: 0;
    left: 0;
    background: center url(../../images/frame/frame-duplicate-white.png),
                #4d4d4d;
    border: none;
    border-right: 3px solid gray;
    border-top: 3px solid gray;
  }

  .number-frame {
    top: 0;
    left: 0;
    background: #4d4d4d;
    font-weight: bold;
    font-size: 1.5rem;
    color: white;
    border-right: 3px solid gray;
    border-bottom: 3px solid gray;
  }

  .add-frame {
    box-sizing: border-box;
    border: 3px solid gray;
    background: none;
    color: white;
    font-size: 8rem;
  }
  
  .add-frame:hover {
    background: gray;
    border: 3px solid #ffd900;
  }

  .drag-hovered {
    border: 3px dashed white;
  }
