.tool-pad {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.pixel-size {
    display: flex;
    justify-content: space-around;
}

.pixel-size > * {
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: auto;
    box-sizing: border-box;
    border: 2px solid gray;
    line-height: 15px;
}

.pixel-size > *:hover {
    border: 2px solid #b8b8b8;
}

.pixel-size > *.selected {
    border: 2px solid #ffd900;
}

[data-size] > div {
    background: white;
}

.size-1 {
    width: 6px;
    height: 6px;
}

.size-2 {
    width: 8px;
    height: 8px;
}

.size-3 {
    width: 10px;
    height: 10px;
}

.size-4 {
    width: 12px;
    height: 12px;
}

.drawing-tools {
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0;
}

.drawing-tools > * {
    list-style-type: none;
    width: 46px;
    height: 46px;
    background: #696969;
    margin: 2px;
}

.drawing-tools > *.selected {
    background: #bda000;
}

.drawing-tools > *:hover {
    background: gray;
}

.drawing-tools > *.selected:hover {
    background: #bda000;
} 

.drawing-tools .pen::after {
    content: url(./img/tool-pen.png);
}

.drawing-tools .mirror-pen::after {
    content: url(./img/tool-vertical-mirror-pen.png);
}

.drawing-tools .paint-bucket::after {
    content: url(./img/tool-paint-bucket.png);
}

.drawing-tools .same-color-pixel::after {
    content: url(./img/tool-colorswap.png);
}

.drawing-tools .eraser::after {
    content: url(./img/tool-eraser.png);
}

.drawing-tools .stroke::after {
    content: url(./img/tool-stroke.png);
}

.drawing-tools .rectangle {
    content: url(./img/tool-rectangle.png);
}


.drawing-tools .circle::after {
    content: url(./img/tool-circle.png);
}

.drawing-tools .move::after {
    content: url(./img/tool-move.png);
}

.drawing-tools .select-rect::after {
    content: url(./img/tool-rectangle-select.png);
}

.drawing-tools .select-line::after {
    content: url(./img/tool-lasso-select.png);
}

.drawing-tools .lighten-darken::after {
    content: url(./img/tool-lighten.png);
}

.drawing-tools .dither::after {
    content: url(./img/tool-dithering.png);
}

.drawing-tools .color-picker::after {
    content: url(./img/tool-colorpicker.png);
}

.drawing-tools .smooth-pen {
    cursor: default;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 2.7rem;
}

.drawing-tools .smooth-pen::after {
    content: "üñä";
}

