}

.color-switcher {
    position: relative;
    margin: 10px 0 0 10px;
    width: 72px;
    height: 75px;
}

.color-switcher > * {
    position: absolute;
    width: 50px;
    height: 50px;
    border: 3px solid gray;
    box-sizing: border-box;
}

.primary-color {
    z-index: 3;
    top: 0;
    left: 0;
}

.color-switcher input {
    visibility: hidden;
    position: absolute;
}

.secondary-color {
    z-index: 2;
    bottom: 0;
    right: 0;
    background: url(../screens/background-image.png);
}

#switch-colors {
    bottom: 0;
    left: 0;
    width: 18px;
    height: 18px;
    border: none;
}

#switch-colors::after {
    content: url(../images/common-swapcolors-arrow-grey.png);
}

.key-bind-button {
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 5rem;
    color: #bda000;
}

.key-bind-button:hover {
    color: #ffd900;
}

.key-bind-button::after {
    content: '‚å®';
}

